import "./styles.css";

let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

const inputField = document.querySelector(".inputField input");
const addButton = document.querySelector(".inputField button");
const todoList = document.querySelector(".todoList");
const footer = document.querySelector(".footer span");
const clearButton = document.querySelector(".footer button");

function initializeTodos() {
  todos.forEach((todo) => {
    const li = document.createElement("li");
    li.textContent = todo.todoText;
    if (todo.todoComplete) {
      li.classList.add("done");
    }
    const deleteIcon = document.createElement("span");
    deleteIcon.innerHTML = '<i class="fa fa-trash"></i>';
    deleteIcon.addEventListener("click", () => deleteTodo(todo.todoID));
    li.appendChild(deleteIcon);
    li.addEventListener("click", () => toggleComplete(todo.todoID));
    todoList.appendChild(li);
  });

  updatePendingTasks();
}

// Add a new todo
function addTodo(todoText) {
  const newID = todos.length > 0 ? todos[todos.length - 1].todoID + 1 : 0;
  const newTodo = {
    todoID: newID,
    todoText: todoText,
    todoComplete: false
  };
  todos.push(newTodo);

  const li = document.createElement("li");
  li.textContent = todoText;
  const deleteIcon = document.createElement("span");
  deleteIcon.innerHTML = '<i class="fa fa-trash"></i>';
  deleteIcon.addEventListener("click", () => deleteTodo(newID));
  li.appendChild(deleteIcon);
  li.addEventListener("click", () => toggleComplete(newID));
  todoList.appendChild(li);

  updatePendingTasks();
}

// Toggle todo completion
function toggleComplete(todoID) {
  const todoIndex = todos.findIndex((todo) => todo.todoID === todoID);
  todos[todoIndex].todoComplete = !todos[todoIndex].todoComplete;
  const li = todoList.children[todoIndex];
  li.classList.toggle("done");

  updatePendingTasks();
}

// Delete a todo
function deleteTodo(todoID) {
  const todoIndex = todos.findIndex((todo) => todo.todoID === todoID);
  todos.splice(todoIndex, 1);
  todoList.removeChild(todoList.children[todoIndex]);

  updatePendingTasks();
}

// Update the pending tasks count
function updatePendingTasks() {
  const pendingCount = todos.filter((todo) => !todo.todoComplete).length;
  footer.textContent = `You have ${pendingCount} pending task${
    pendingCount !== 1 ? "s" : ""
  }.`;
}

// Clear completed todos
function clearDone() {
  todos = todos.filter((todo) => !todo.todoComplete);
  const completedLiElements = Array.from(
    todoList.getElementsByClassName("done")
  );
  completedLiElements.forEach((li) => todoList.removeChild(li));

  updatePendingTasks();
}

// Add new todo with Enter key
inputField.addEventListener("keyup", (event) => {
  if (event.key === "Enter" && inputField.value.trim() !== "") {
    addTodo(inputField.value.trim());
    inputField.value = "";
  }
});

// Add new todo with plus button
addButton.addEventListener("click", () => {
  if (inputField.value.trim() !== "") {
    addTodo(inputField.value.trim());
    inputField.value = "";
  }
});

clearButton.addEventListener("click", clearDone);

initializeTodos();
